<template>
  <div>
    <NewMeetingForm :meetings="meetings" @added="addNewMeeting($event)"></NewMeetingForm>
    <span v-if="meetings.length == 0">Brak zaplanowanych spotkań.</span>
    <h3 v-else>Zaplanowane zajęcia ({{ meetings.length }})</h3>
    <MeetingsList :meetings="meetings" :username="username" @attend="addMeetingParticipant($event)"
      @unattend="removeMeetingParticipant($event)" @delete="deleteMeeting($event)"></MeetingsList>
  </div>
</template>

<script>
import NewMeetingForm from "./NewMeetingForm";
import MeetingsList from "./MeetingsList";
import axios from "axios";

export default {
  components: { NewMeetingForm, MeetingsList },
  props: {
    username: String
  },
  data() {
    return {
      meetings: [],
      idError: false
    }
  },
  created: async function () {
    let dbDataMeetings = [];
    let dbParticipants = [];
    await axios.get('/api/meetings')
      .then(response => {
        dbDataMeetings = response.data;
        console.log(dbDataMeetings);
        dbDataMeetings.forEach(element => {
          axios.get('/api/meetings/' + element.id + '/participants')
            .then(response => {
              dbParticipants = response.data;
              let participants = [];
              dbParticipants.forEach(element => {
                participants.push(element.login);
              });
              element.participants = participants;

              this.meetings.push(element);
            })
            .catch(error => console.log(error));
        });
      })
      .catch(error => console.log(error));
  },
  methods: {
    addNewMeeting(meeting) {
      if (meeting.description == null) {
        meeting.description = '';
      }

      axios.post('/api/meetings', meeting)
        .then(response => {
          meeting.id = response.data.id;
          this.meetings.push(meeting);
        })
        .catch(error => console.log(error));
      console.log(meeting);
    },
    addMeetingParticipant(meeting) {
      const meetingIndex = this.meetings.indexOf(meeting);
      const participantsList = this.meetings.at(meetingIndex).participants;

      axios.post('/api/meetings/${meeting.id}/participants', { login: this.username })
        .then(response => console.log('Dodano uczestnika do spotkania'))
        .catch(error => console.log(error));
      participantsList.push(this.username);
      console.log(this.meetings);
    },
    removeMeetingParticipant(meeting) {
      axios.delete('/api/meetings/' + meeting.id + '/participants/' + this.username)
        .then(response => console.log('Usunięto uczestnika ze spotkania'))
        .catch(error => console.log(error));
      meeting.participants.splice(meeting.participants.indexOf(this.username), 1);
    },
    deleteMeeting(meeting) {
      axios.delete('/api/meetings/' + meeting.id)
        .then(response => console.log('Usunięto spotkanie'))
        .catch(error => console.log(error));
      this.meetings.splice(this.meetings.indexOf(meeting), 1);
      this.error = false;
    }
  }
}
</script>

<style scoped>
.error {
  padding-top: -40px;
  padding-bottom: 20px;
  color: #F00;
}
</style>